exports.push([module.id, ".background[data-v-0d15e0c6]{fill:#fff;opacity:.15}", "",{"version":3,"sources":["/home/sangfor/harvester-ui/components/graph/components/graph/ProgressArc.vue"],"names":[],"mappings":"AAmFA,6BACA,SAAA,CACA,WACA","file":"ProgressArc.vue","sourcesContent":["<script>\nimport * as d3 from 'd3';\nexport default {\n  props: {\n    progress: {\n      type:    Number,\n      default: 0\n    },\n    graphWidth:      {\n      type:     Number,\n      required: true\n    },\n  },\n  data() {\n    return { tweenedProgress: this.progress };\n  },\n  computed: {\n    progressColor() {\n      const end = [127, 189, 132];\n      const init = [189, 127, 127];\n\n      return init.map((val, i) => {\n        return (val + ((end[i] - val) * this.progress / 100)).toFixed(0);\n      });\n    },\n    outerRadius() {\n      return this.graphWidth / 2;\n    },\n    innerRadius() {\n      return this.outerRadius - this.graphWidth * 0.05;\n    },\n    endAngle() {\n      return this.tweenedProgress * 2 * Math.PI / 100;\n    },\n    fullArc() {\n      const vm = this;\n      const arc = d3.arc();\n\n      return arc({\n        innerRadius:  vm.innerRadius,\n        outerRadius:  vm.outerRadius,\n        endAngle:     2 * (Math.PI),\n        startAngle:   0\n      });\n    },\n    arc() {\n      // progress / 100 == endAngle/ 2pi\n      const vm = this;\n      const arc = d3.arc();\n\n      return arc({\n        innerRadius: vm.innerRadius,\n        outerRadius: vm.outerRadius,\n        endAngle:    vm.endAngle,\n        startAngle:  0\n      });\n    }\n  },\n  watch: {\n    progress(newProgress, oldProgress) {\n      this.tweenedProgress = newProgress;\n    }\n  }\n\n};\n\n</script>\n\n<template>\n  <div>\n    <svg\n      preserveAspectRatio=\"xMidYMid meet\"\n      :viewBox=\"`0 1 ${graphWidth} ${graphWidth}`\"\n    >\n      <g :transform=\"`translate(${graphWidth/2}, ${graphWidth/2}) rotate(180)`\">\n        <path class=\"background\" :d=\"fullArc\"></path>\n        <path :style=\"{fill: `rgb(${progressColor[0]}, ${progressColor[1]}, ${progressColor[2]})`}\" :d=\"arc\"></path>\n      </g>\n    </svg>\n  </div>\n</template>\n\n<style scoped>\n  .background{\n    fill:white;\n    opacity: 0.15;\n  }\n</style>\n"]}]);
